import {PagedResult} from './../../../../../../core/models/common/page/PagedResult';
import {Component, OnInit} from '@angular/core';
import {AuthorAdminViewModel} from 'src/app/core/models/admin/AuthorViewModel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {AuthorAdminService} from 'src/app/core/services/admin/author/author-admin.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';

@Component({
	selector: 'app-author-index',
	templateUrl: './author-index.component.html',
	styleUrls: ['./author-index.component.less'],
	providers: [DestroyService]
})
export class AuthorIndexComponent implements OnInit {
	pagingResult: PagedResult<AuthorAdminViewModel>;
	pageIndex: number = 1;
	searchKeyWord: string;
	isLoading = true;
	authorSearchForm: FormGroup;
	constructor(
		private destroy$: DestroyService,
		private notificationService: NotificationService,
		private authorAdminService: AuthorAdminService,
		private formBuilder: FormBuilder
	) {}

	ngOnInit(): void {
		this.get();
		this.initSearchForm();
		this.checkSearchFormValueChange();
	}
	initSearchForm() {
		this.authorSearchForm = this.formBuilder.group({
			search: ['', [Validators.required, Validators.minLength(1)]]
		});
	}
	get() {
		this.authorAdminService
			.getPaging({
				pageIndex: this.pageIndex,
				pageSize: 10,
				searchKeyWord: this.searchKeyWord ?? '',
				languageCode: 'vn'
			})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.pagingResult = result.resultObj;
							console.log(this.pagingResult);
							this.isLoading = false;
						} else {
						}
					} else {
					}
				} else {
				}
			});
	}
	pageChanged(page: number) {
		this.pageIndex = page; // receive the page number
		// display the sort button by previous sort
		this.get(); // navigated by sort and page
	}
	changeStatus(status: boolean, authorId: number) {
		console.log(status);
		this.authorAdminService
			.changeStatus({authorId: authorId, status: status})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.notificationService.showSuccessNotification(result.message);
						} else {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				}
			});
	}
	checkSearchFormValueChange() {
		this.authorSearchForm.valueChanges.subscribe((result) => {
			console.log(result.search);
			this.searchKeyWord = result.search;
			this.pageIndex = 1;
			this.get();
		});
	}
	deleteAuthor(authorId: number) {
		console.log(authorId);
		this.authorAdminService
			.delete(authorId)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.notificationService.showSuccessNotification('Xóa Thành Công');
						} else {
						}
					} else {
						this.notificationService.showSuccessNotification(result.message);
					}
				} else {
				}
			});
	}
	cancel(){}
}
