import {Injectable} from '@angular/core';
import { PagingRequestBase } from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {AdminUrlBase} from './admin-url-base';

@Injectable({
	providedIn: 'root'
})
export class AdminUrlAuthor extends AdminUrlBase {
	create(): string {
		return this.adminUrl + '/author';
	}
    edit(): string {
		return this.adminUrl + '/author';
	}
	getPaging(pagingRequestBase:PagingRequestBase): string {
		return this.adminUrl + `/author/authors?searchKeyWord=${pagingRequestBase.searchKeyWord}&pageIndex=${pagingRequestBase.pageIndex}&pageSize=${pagingRequestBase.pageSize}&languageCode=${pagingRequestBase.languageCode}`;
	}
	get(id:number): string {
		return this.adminUrl + `/author/${id}`;
	}
	delete(id:number): string {
		return this.adminUrl + `/author/${id}`;
	}
	changeStatus(): string {
		return this.adminUrl + `/author/status`;
	}
}
