import {PagingRequestBase} from './../../../models/common/page/pagingrequest/PagingRequestBase';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {AdminUrlAuthor} from './../../../utils/URL/admin-url/admin-url-author';
import {Injectable} from '@angular/core';
import {AuthorAdminCreateViewModel, AuthorAdminEditViewModel, AuthorAdminViewModel} from 'src/app/core/models/admin/AuthorViewModel';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';

@Injectable({
	providedIn: 'root'
})
export class AuthorAdminService {
	constructor(private adminUrlAuthor: AdminUrlAuthor, private http: HttpClient) {}
	create(authorAdminCreateViewModel: AuthorAdminCreateViewModel): Observable<ApiResult<boolean>> {
		return this.http.post(this.adminUrlAuthor.create(), authorAdminCreateViewModel);
	}
	getPaging(pagingRequestBase: PagingRequestBase): Observable<ApiResult<PagedResult<AuthorAdminViewModel>>> {
		return this.http.get(this.adminUrlAuthor.getPaging(pagingRequestBase));
	}
	get(id: number): Observable<ApiResult<AuthorAdminViewModel>> {
		return this.http.get<ApiResult<AuthorAdminViewModel>>(this.adminUrlAuthor.get(id));
	}
	delete(id: number): Observable<ApiResult<boolean>> {
		return this.http.delete(this.adminUrlAuthor.delete(id));
	}
	edit(authorAdminEditViewModel: AuthorAdminEditViewModel): Observable<ApiResult<boolean>> {
		return this.http.put(this.adminUrlAuthor.edit(), authorAdminEditViewModel);
	}
	changeStatus({authorId:number, status:boolean}): Observable<ApiResult<boolean>> {
		return this.http.put(this.adminUrlAuthor.changeStatus(), {authorId:number, status:boolean});
	}
}
